$opensansreg: "OpenSansRegular",Arial,sans-serif;
$opensansbold: "OpenSansBold",Arial,sans-serif;
$opensanssemibold: "OpenSansSemiBold",Arial,sans-serif;
$opensansitalic: "OpenSansItalic",Arial,sans-serif;
